@import '../../styles/main.scss';

.box {
  margin: auto;
  height: 100vh;

  overflow: hidden;
  z-index: 0;

  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;

  text-align: center;

  background: linear-gradient(
      0deg,
      rgba(10, 10, 17, 0.2) 0%,
      rgba(10, 10, 17, 0.2) 100%
    ),
    linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
    linear-gradient(82deg, #0a0a11 11.89%, rgba(10, 10, 17, 0) 82.65%),
    url(../../img/edward-howell-unsplash-mob.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        0deg,
        rgba(10, 10, 17, 0.2) 0%,
        rgba(10, 10, 17, 0.2) 100%
      ),
      linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
      linear-gradient(82deg, #0a0a11 11.89%, rgba(10, 10, 17, 0) 82.65%),
      url(../../img/edward-howell-unsplash-mob@2x.jpg);
  }

  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;

  @media screen and (min-width: 768px) {
    background: linear-gradient(
        0deg,
        rgba(10, 10, 17, 0.2) 0%,
        rgba(10, 10, 17, 0.2) 100%
      ),
      linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
      linear-gradient(78deg, #0a0a11 14.73%, rgba(10, 10, 17, 0) 52.34%),
      url(../../img/edward-howell-unsplash-tablet.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background: linear-gradient(
          0deg,
          rgba(10, 10, 17, 0.2) 0%,
          rgba(10, 10, 17, 0.2) 100%
        ),
        linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
        linear-gradient(78deg, #0a0a11 14.73%, rgba(10, 10, 17, 0) 52.34%),
        url(../../img/edward-howell-unsplash-tablet@2x.jpg);

      background-size: contain;
      background-repeat: no-repeat;
      background-position: right;
    }

    background-size: contain;
    background-repeat: no-repeat;
    background-position: right;
  }

  @media screen and (min-width: 1440px) {
    background: linear-gradient(
        0deg,
        rgba(10, 10, 17, 0.2) 0%,
        rgba(10, 10, 17, 0.2) 100%
      ),
      linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
      linear-gradient(85deg, #060609 5.92%, rgba(10, 10, 17, 0) 51.07%),
      url(../../img/edward-howell-unsplash-dt.jpg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          0deg,
          rgba(10, 10, 17, 0.2) 0%,
          rgba(10, 10, 17, 0.2) 100%
        ),
        linear-gradient(1deg, #0a0a11 -0.56%, rgba(10, 10, 17, 0) 21.93%),
        linear-gradient(85deg, #060609 5.92%, rgba(10, 10, 17, 0) 51.07%),
        url(../../img/edward-howell-unsplash-dt@2x.jpg);

      background-size: contain;
      background-repeat: no-repeat;
      background-position: right;
    }

    background-size: contain;
    background-repeat: no-repeat;
    background-position: right;
  }
}

